import React from 'react'
import {Panel, PanelHeader} from '@vkontakte/vkui';
import { Group, Div, Button } from '@vkontakte/vkui';
import './style.css'

const Default = props => {

    const data = props.data;

    const add = () => {
        props.add(data.perClick);
    }

    const transform = () => {
        let score = JSON.stringify(props.score).split('');
        let length = score.length;
        const setLength = ( length ) => {
            let sum = '';
            for(let i = 0; i < length - 1; i++) {
            sum += score[i];
            }
            return sum;
        }
        if(length > 3) {
            if(length % 3 === 0) {
                if(length > 5) {
                return score[0] + score[1] + score[2] + '.' + score[3] + score[4] + score[5];
                }
                if(length === 5) {
                    return score[0] + score[1] + '.' + score[2] + score[3] + score[4];
                }
                if(length === 4) {
                    return score[0] + '.' + score[1] + score[2] + score[3];
                }
            } else {
                if(length > 5) {
                    return setLength(Math.floor(length / 3) * 3);
                }
            }
        }
        return score;
    }

    

    transform();

    return (
      <Panel id={props.id} theme="white">
        <PanelHeader>
            Кликер(0.0.6)
		</PanelHeader>
        <Div style={{ display: 'flex' }}>
            <Button stretched onClick={props.go} data-to="shop" style={{ marginRight: 8 }}>
                Магазин
            </Button>
            <Button stretched onClick={props.go} data-to="settings" style={{ marginRight: 8 }}>
                Настройки
            </Button>
            <Button stretched>
                Рулетка(Не работает)
            </Button>
        </Div>
        <Div>
            <Div className="score-container">
                <p>{Math.round(props.score)} #</p>
            </Div>       
            <Div className="button-container">
                <button className="button-click" onClick={() => add()}>
                    <h1>H+</h1>
                    <span>{data !== null ? data.auto : null} / сек</span>
                </button>
            </Div>
        </Div>
      </Panel>
    )
}
  
  export default Default
